body {
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
    background-color: #f4f7fb;
  }
  
  .login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 60px);
    padding: 20px;
    max-width: 400px;
    margin: 0 auto;
  }
  
  h2 {
    font-size: 28px;
    color: #333;
    margin-bottom: 20px;
    text-align: center;
  }
  
  input {
    width: 100%;
    padding: 12px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 6px;
    font-size: 16px;
    background-color: #fff;
    transition: border-color 0.3s ease;
  }
  
  input:focus {
    outline: none;
    border-color: #007bff;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.3);
  }
  
  button {
    width: 100%;
    padding: 12px;
    margin: 15px 0;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 6px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  button:active {
    background-color: #004085;
  }
  
  p {
    font-size: 14px;
    color: #666;
    margin-top: 15px;
    text-align: center;
  }
  
  .toggle-link {
    color: #007bff;
    cursor: pointer;
    text-decoration: none;
    font-weight: 500;
  }
  
  .toggle-link:hover {
    text-decoration: underline;
    color: #0056b3;
  }
  
  @media (max-width: 480px) {
    .login-container {
      padding: 15px;
      max-width: 90%;
    }
  
    h2 {
      font-size: 24px;
    }
  
    input, button {
      font-size: 14px;
      padding: 10px;
    }
  }