.onboarding-container {
    max-width: 500px;
    margin: 40px auto;
    padding: 20px;
    border-radius: 12px;
    background: #f4f4f4;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .onboarding-form label {
    display: block;
    margin-bottom: 15px;
    font-weight: 500;
  }
  
  .onboarding-form input,
  .onboarding-form select {
    width: 100%;
    padding: 8px;
    margin-top: 4px;
    border-radius: 6px;
    border: 1px solid #ccc;
    box-sizing: border-box;
  }
  
  .onboarding-form button {
    margin-top: 20px;
    width: 100%;
    padding: 10px;
    background-color: #3f51b5;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
  }
  
  .onboarding-form button:hover {
    background-color: #303f9f;
  }
  